/*Key Frames ==========================================================================*/
@-webkit-keyframes rollLeft {
  0% {
    -webkit-transform: translateX(200%) rotate(180deg);
            transform: translateX(200%) rotate(180deg);
    /* opacity: 0; */
  }
  100% {
    -webkit-transform: translateX(0) rotate(0deg);
            transform: translateX(0) rotate(0deg);
    /* opacity: 1; */
  }
}
@keyframes rollLeft {
  0% {
    -webkit-transform: translateX(200%) rotate(180deg);
            transform: translateX(200%) rotate(180deg);
    /* opacity: 0; */
  }
  100% {
    -webkit-transform: translateX(0) rotate(0deg);
            transform: translateX(0) rotate(0deg);
    /* opacity: 1; */
  }
}

@-webkit-keyframes shrink {
  0% {
    width: 0%;
  }
  100% {
    width: 200%;
  }
}

@keyframes shrink {
  0% {
    width: 0%;
  }
  100% {
    width: 200%;
  }
}

@-webkit-keyframes tada {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

@keyframes tada {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

@-webkit-keyframes fallDown {
  0% {
    -webkit-transform: translateX(200%) rotate(180deg);
            transform: translateX(200%) rotate(180deg);
  }
  40% {
    -webkit-transform: translateX(0) rotate(0deg) translateY(100px);
            transform: translateX(0) rotate(0deg) translateY(100px);
  }
  50%{
    -webkit-transform: translateY(100px) ;
            transform: translateY(100px) ;
  }
  70%{
    -webkit-transform: translateY(100px) rotate(200deg);
            transform: translateY(100px) rotate(200deg); 
    -webkit-transform-origin: 20%; 
            transform-origin: 20%;
  }
  /* 75%{
    transform: translateY(100px) rotate(150deg); 
    transform-origin: 20%;
  } */
  100%{
    -webkit-transform: translateY(1000%);
            transform: translateY(1000%);
  }
}

@keyframes fallDown {
  0% {
    -webkit-transform: translateX(200%) rotate(180deg);
            transform: translateX(200%) rotate(180deg);
  }
  40% {
    -webkit-transform: translateX(0) rotate(0deg) translateY(100px);
            transform: translateX(0) rotate(0deg) translateY(100px);
  }
  50%{
    -webkit-transform: translateY(100px) ;
            transform: translateY(100px) ;
  }
  70%{
    -webkit-transform: translateY(100px) rotate(200deg);
            transform: translateY(100px) rotate(200deg); 
    -webkit-transform-origin: 20%; 
            transform-origin: 20%;
  }
  /* 75%{
    transform: translateY(100px) rotate(150deg); 
    transform-origin: 20%;
  } */
  100%{
    -webkit-transform: translateY(1000%);
            transform: translateY(1000%);
  }
}


